header {
	color: white;
	font-weight: 400;

	.top {
		background-color: $color-grey;
		padding: 8px 0;

		.flex {
			align-items: center;
		}

		.contact {
			font-size: 0.75em;
			border-right: 1px solid #aaaaaa;
			flex: 1;
			.flex {
				align-items: center;
				& > div {
					line-height: 1;
					flex: 1;

					.wrapper{
						padding: 0 10px;
						color: white;

						a {
							color: white;
							text-decoration: none;
						}

						i {
							color: lighten($color-orange, 10%);
							float: left;
					    font-size: 24px;
					    margin: -2px 5px 0 0;
						}
					}
					.val {
						margin-top: 5px;
						font-size: 0.95em;
					}
				}
			}
		}
		.social {
			.flex {
				align-items: center;
				justify-content: flex-start;

				& > div {
					padding: 0 2px;
				}

				a {
					padding: 0;
					width: 20px;
					line-height: 20px;
					display: block;
					text-align: center;
					color: white;
					background-color: #aaaaaa;
					@include border-radius(10px);
				}
			}
		}
		.actions {
			padding: 0 20px;
			flex-basis: 300px;
			& > div.flex {
				align-items: center;
				justify-content: space-between;
				& > div {
					flex: 1;
					padding: 0 5px;
					button {
						@include border-radius(5px);
						border:  none;
						font-weight: bold;
						font-size: 0.67em;
						padding: 10px;
						display: block;
						width: 100%;
					}
					&.visszahivas{
						button {
							background: $color-red;
							color: white;
							text-transform: uppercase;
						}
					}
					&.ajanlatkeres{
						button {
							background: saturate(lighten($color-orange, 5%), 10);
							color: $color-grey;
							text-transform: uppercase;
						}
					}
				}
			}
		}
		.tudastar {
			padding-left: 10px;
			height: 30px;

			& > .flex > div {
				padding: 0 3px;
				height: 30px;
				line-height: 30px;
			}
			.ico{
				.wrap{
					width: 30px;
					height: 30px;
					line-height: 30px;
					background-color: $color-orange;
					color: white;
					text-align: center;
					font-size: 20px;
					@include border-radius(30px);
				}
			}
			& > .flex > .text {
				text-transform: uppercase;
			}
			.dropmenu{
				.dropmenu-container{
					cursor: pointer;
					background-color: white;
					color: $color-grey;
					font-size: 0.7em;
					text-transform: none !important;

					.text {
						padding: 0 10px;
					}

					.arrow {
						height: 30px;
						width: 30px;
						text-align: center;
						padding: 0;
						color: $color-red;
						font-size: 14px;
						background-color: lighten($color-grey, 70%);
					}
				}
			}
		}
	}

	.main {
		background-color: $color-red;
		padding: 10px 0;

		& > .pw > .flex {
			align-items: center;
		}

		.logo {
			flex-basis: $size-sidebar;
		}

		.searcher {
			flex: 1;
			text-align: right;
			.searchform {
				padding: 2px;
				float: right;
				width: 70%;
				background: white;

				.flex {
					.input {
						flex: 1;
					}
					.button {
						flex-basis: 85px;
						button {
							width: 100%;
							text-align: center;
							text-transform: uppercase;
							font-size: 0.7em;
							color: lighten($color-red, 10%);

							i {
								font-size: 18px;
								vertical-align: middle;
								margin: -1px 5px 0 0;
							}
						}
					}
				}

				input[type=text] {
					background: transparent;
					border: none;
					width: 100%;
					color: $color-red;

					@include border-radius-left(5px);
				}

				input[type=text],
				button {
					line-height: 30px;
					padding: 0 5px;
				}

				button {
					background: transparent;
					color: $color-red;
					border: none;
					border-left: 1px solid #e6e6e6;
					padding: 0 8px;
					vertical-align: middle;
					cursor: pointer;
				}
			}
		}
		.actions {
			flex-basis: 35%;
			padding-left: 20px;
			font-size: 0.9em;

			& > .flex {
				align-items: center;
				justify-content: space-between;
				& > div{
					height: 56px;
				}
				& > div:not(.div) {
					text-align: center;
					flex-basis: 65px;
				}
				& > div.cart{
					flex: 1;
				}
				& > .div{
					flex-basis: 6px;
					position: relative;

					&:after{
						height: 70%;
						position: absolute;
						width: 1px;
						content: "";
						top: 0;
						-webkit-transform: translate(50%, -50%);
          	transform: translate(50%, -50%);
						right: 50%;
						top: 50%;
						background-color: white;
					}
				}
			}

			.badge {
				pointer-events: none;
				position: absolute;
				right: 2px;
				bottom: 2px;
				background-color: #ffcc00;
				color: black;
				font-size: 11px;
				width: 20px;
				z-index: 10;
				height: 20px;
				padding: 0;
				line-height: 20px;
				text-align: center;
				border-radius: 20px;
			}

			.cart {
				padding: 0 10px;
				.flex {
					align-items: center;
					& > div.ico {
						flex-basis: 35px;
					}
					& > div.info {
						flex: 1;
					}
				}

				.badge {
					bottom: 5px;
					right: -10px;
				}
				.info {
					line-height: 1;
					padding: 0 5px 0 15px;
					text-align: left;
					.h {
						border-bottom: 1px solid #ffffffb0;
				    padding: 0px 0 2px 0;
				    margin: 0 0 2px 0;
					}
					.l {
						font-size: 0.7em;
						margin: 5px 0;
					}
					.cash {
						font-weight: bold;
						font-size: 1.3em;
					}
				}
			}

			a.holder {
				padding: 5px;
				display: block;
				color: white;
				text-decoration: none;
				-webkit-transition: all 0.3s;
  			transition: all 0.3s;


				&:hover{
					background-color: #971116;
					-webkit-transform: scale(0.9);
         	transform: scale(0.9);
				}
			}

			i {
				font-size: 35px;
				vertical-align: middle;
				height: 35px;
			}

			.ico {
				height: 35px;
				position: relative;
				text-align: center;

				img {
					max-height: 35px;
					height: 35px;
				}
			}


		}
	}

	.bottom {
		background-color: darken( $color-red, 10% );
		padding: 5px 0;
	}
	.sec-bottom{
		background-color: #eceff2;
	}

	a, a:visited {
		color: white;
	}

	.logo {
		img {
			max-height: 50px;
			max-width: 100%;
		}

		position: relative;;

		.cimke {
			display: none;
			position: absolute;
			width: 100px;
			height: 15px;
			background: url('http://devsup-shop-cp.casada.hu/src/images/webshop_cimke.png') no-repeat center;
			bottom: -10px;
			pointer-events: none;
		}
	}
}


.general-sidebar {
	width: 20%;
	float: left;
	display: table-cell;
	vertical-align: top;

	.menu {
		li.menu-item {
			display: table;
			width: 100%;
			margin: 0 -1px -1px 0;
			border: 1px solid lighten( $color-sec, 1% );
			background: lighten( $color-sec, 9%);

			a {
				display: table-cell;
				font-size: 1.1em;
				line-height: 1.5;
				text-transform: uppercase;
				color: darken( $color-sec, 50%);
				font-family: $font-sans-narrow;
				font-weight: 700;
				padding: 5px 10px;
				vertical-align:middle;
				text-decoration: none;

				img {
					float: left;
					margin-right: 5px;
					vertical-align: middle;
				}

				i {
					width: 25px;
					height: 25px;
					font-size: 25px;
				}

				span {
					line-height: 25px;
				}
			}

			span.icon {
				width: 35px;
				text-align:center;
				display: table-cell;
				vertical-align:middle;
				padding-left: 8px;
			}

			i.dashicons {
				line-height: 28px;
    			font-size: 1.2em;
			}
		}
		li.blank-divider{
			background: transparent;
			border: none;
			padding: 10px 0;
    	display: block;
		}
		li.text-item-header{
			font-size: 1.1em;
			text-transform: uppercase;
			color: black;
			font-weight: bold;
			font-family: "PT Sans Narrow", sans-serif;
			text-align: center;
		}
		li.kat-child{
			border-left: 2px solid #cccccc;
			a {
				font-weight: normal;
				font-size: 0.9em;
				text-transform: none;
			}
		}
		li.text-thin a {
			font-weight: 400;
		    font-size: 1.2em;
		    display: block;
		    padding-left: 15px;
		    width: 100%;
		    text-transform: lowercase;
		}
		.webshop-cat-items {

			li.cat-item{
				border-bottom: 1px solid #DCDCDC;

				a {
					font-weight: 400;
					font-size: 1.2em;
					display: block;
					padding-left: 15px;
					width: 100%;

					text-transform: lowercase;
				}
			}
		}

		li.menu-item.has-icon.two-row:not(.text-thin) {
			span.ic {
				display: table-cell;
				vertical-align: middle;
			}
			span.text {
				display: table-cell;
				vertical-align: middle;
				line-height: 1;
				padding-left: 8px;
			}
		}
	}
}
